import React from 'react';
import { View, Text } from 'react-native';

const LayoutTemplate = ({ children }) => {


    return (
        <>
            <View>
                <Text>LAYOUT HEADER</Text>
            </View>
            <View>
                {children}
            </View>
            <View>
                <Text>LAYOUT FOOTER</Text>
            </View>
        </>
    );
};

export default LayoutTemplate;
